/*
* Fullwidth Layout
*/

$navbar-fullwidth-nav-color: $navbar-nav-color;
$navbar-fullwidth-nav-background: $navbar-nav-background;
$navbar-fullwidth-nav-hover-color: $navbar-nav-hover-color;
$navbar-fullwidth-nav-hover-background: $navbar-nav-hover-background;
$navbar-fullwidth-nav-active-color: $navbar-nav-active-color;
$navbar-fullwidth-nav-active-background: $navbar-nav-active-background;

.rd-navbar-fullwidth {
  display: block;


  .rd-navbar-search-form , .rd-navbar-live-search-results{
  left: 60%;
  right: 15px;
}


   .rd-navbar-nav > li > a::before{
     display: none;
   }
  // RD Navbar Panel
  .rd-navbar-panel{
    text-align: center;
    padding: 20px 0;

  }

  .rd-navbar-nav-wrap{
    padding-right: 111px;
    position: relative;

    .rd-navbar-megamenu{
      left: 0;
      right: 0;
    }
  }

  .rd-navbar-search-toggle{
    right: 0;
  }

  // RD Navbar Nav
  .rd-navbar-nav {
    width: 100%;
    display: table;
    table-layout: fixed;

    > li {
      display: table-cell;

      > a {
        display: block;
        text-align: center;
        padding: 0px;
      }
    }
  }

  // RD Navbar States
  &.rd-navbar--is-stuck {
    @include transform(translateY(-120px));

    .rd-navbar-panel{
      display: none;
    }
  }

  .rd-navbar-fullwidth--visible{
    display: block;
  }

  .rd-navbar-fullwidth--hidden{
    display: none;
  }
}