.ui-to-top {
  width: 51px;
  height: 51px;
  background: $default-1;
  border-radius: 50%;
  //box-shadow: $box-shadow-ambient, $box-shadow-key-light;
  position: fixed;
  right: 15px;
  bottom: 15px;
  overflow: hidden;
  text-align: center;
  text-decoration: none;
  z-index: 20;
  @include transform(translateY(100px));
  &:hover {
    background: $primary;
    text-decoration: none;
  }
  &:before {
    //content: "top";
    color: $white;
    text-transform: uppercase;
    font-size: 14px;
    line-height: 51px;
  }
  &.active{
    @include transform(translateY(0));
  }
}
.mobile .ui-to-top,
.tablet .ui-to-top {
  display: none !important;
}
@media (min-width: $screen-xs-min){
  .ui-to-top {
    right: 40px;
    bottom: 40px;
  }
}


