/*
* @section      Main Styles
* @description  This section holds template default elements style declarations
*/

html {
  font-size: 60px;

  @media (min-width: $screen-sm-min) and (max-width: $screen-md-min - 1) {
    font-size: 70px;
  }

  @media (min-width: $screen-md-min) and (max-width: $screen-lg-min - 1) {
    font-size: 80px;
  }

  @media (min-width: $screen-lg-min) {
    font-size: 100px;
  }

  .old-ie {
    display: none;
  }

  &.lt-ie11 {
    .old-ie{
      display: block;
    }
  }
}

body {
  font-family: $default-font-family;
  font-weight: $default-font-weight;
  font-size: $default-font-size;
  line-height: $default-line-height;
  -webkit-text-size-adjust: none;
  color: $content-color;
  background: $content-bg;

  @media (min-width: $screen-sm-min) and (max-width: $screen-md-min - 1) {
    font-size: 14px;
  }

  @media (min-width: $screen-md-min) {
    font-size: $default-font-size;
    line-height: $default-line-height;
  }
}


/*
* @subsection   Typography
* @description  Describes default font settings for basic html elements and
*               css classes
*/

h1, h2, h3, h4, h5, h6,
.heading-1, .heading-2, .heading-3, .heading-4, .heading-5, .heading-6 {
  display: block;
  color: $grayscale-darken;
  font-weight: 400;

  a:hover {
    color: $primary;
  }
}

h1, .heading-1 {
  font-size: 38px;
  line-height: 1.33;

  @media (min-width: $screen-sm-min) {
    font-size: 62px;
    line-height: 1.16;
  }

  > .heading-2{
    margin-bottom: rem(33px);
  }
}

h2, .heading-2 {
  font-size: 22px;
  line-height: 1.5;
  color: $default-1;
  text-transform: uppercase;
  letter-spacing: 5px;
  font-weight: 300;
  mark{
    padding: 5px 60px;
  }

  @media (min-width: $screen-sm-min) {
    font-size: 50px;
    line-height: 1.32;
  }
}

h3, .heading-3 {
  font-size: 20px;
  line-height: 1.5;
  color: $default-1;
  font-weight: bold;
  @media (min-width: $screen-sm-min) {
    font-size: 20px;
    line-height: 1.37;
  }
}

h4, .heading-4 {
  font-size: 26px;

  @media (min-width: $screen-sm-min) {
    font-size: 18px;
    line-height: 1.47;
  }
}

h5, .heading-5 {
  font-size: 22px;

  @media (min-width: $screen-sm-min) {
    font-size: 16px;
    line-height: 1.66;
  }
}

h6, .heading-6 {
  font-size: 14px;

  @media (min-width: $screen-sm-min) {
    font-size: 14px;
    line-height: 1.5;
  }
}

p, .list, .contact-info {
  font-weight: 400;

  a:hover {
    color: $primary;
  }
}

mark {
  display: inline-block;
  padding: 5px;
  color: #FFF;
  background: $primary;
}

em {
  color: $primary;
}

small, .small {
  display: block;
  font-size: 0.875em;
  line-height: 1.643em;
}

.big {
  font-size: 1.2em;
}

time {
  &:before{
    padding-right: 8px;
  }
}

hr{
  height: 1px;
  width: 100%;
  display: none;
  padding: 0;
  background: rgba($default, 0.04);
}


