/*
* @subsection   Inline List
*/

.inline-list {
  > li {
    display: inline-block;
    a {
      color: $white;
      opacity: 0.5;
      transition: .3s all ease;
      padding: 0 4px;
      &:before{
        padding: 0 4px;
      }
      &:hover {
        opacity: 1;
      }
    }
    margin-top: 0;
    padding: 0 10px;
  }
}


/*
* @subsection   Marked List
*/

.marked-list {
  text-align: left;

  > li {
    position: relative;
    padding-left: 30px;

    &:before {
      content: "\f105";
      position: absolute;
      left: 0;
      top: 0;
      font-weight: 400;
      font-size: 1em;
      font-family: $fa;
      color: $primary;
    }

    & + li {
      margin-top: 10px;
    }

    > a:hover {
      color: $primary;
    }
  }
}

/*
* @subsection   Index List
*/

.index-list {
  counter-reset: li;

  > li {
    > .index-list__counter{
      &:before {
        content: counter(li, decimal-leading-zero) '.';
        counter-increment: li;
      }
    }
  }

  // Sets offset between list rows
  @include make-flow-offset(30px);
}


/*
* @subsection   Ordered List
*/

.ordered-list {
  > li {
    padding-left: 20px;
    list-style-type: decimal;
  }
}


