/*
* @subsection   Thumb
*/
.thumb {
  position: relative;
  display: block;
  overflow: hidden;
  img {
    width: 100%;
  }
  a > img {
    width: 100%;
  }
  &__titel{
    position: absolute;
    top: 52%;
    left: 50%;
    opacity: 0;
    text-transform: uppercase;
    font-size: 18px;
    font-weight: 600;
    transform: translateX(-50%);
  }
  &__overlay1{
    position: absolute;
    left: 0px;
    top: 0px;
    bottom: 0px;
    right: 0px;
    background: rgba($primary, .7);
    opacity: 1;
    text-align: center;
    transition: .3s;
    > *{
      vertical-align: middle;
      @include transform(translateY(30px));
      transition: .3s all ease;
    }
    span{
      display: inline-block;
      position: absolute;
      z-index: 2;
      left: 50%;
      font-weight: bold;
      top: 50%;
      font-size: 20px;
      color: $white;
      @include transform (translate(-50%,-50%));
    }
  }
  &__overlay{
    position: absolute;
    left: 10px;
    top: 10px;
    bottom: 10px;
    right: 10px;
    background: rgba($default-1, .7);
    opacity: 0;
    text-align: center;
    transition: .3s;
    > *{
      vertical-align: middle;
      @include transform(translateY(30px));
      transition: .3s all ease;
    }
    &:before{
      content: '';
      display: inline-block;
      position: absolute;
      z-index: 2;
      left: 50%;
      top: 50%;
      @include transform (translate(-50%,-50%));
      background: url("../images/thumb.png") no-repeat;
      width: 32px;
      height: 32px;
    }
  }
  &__overlay{
    &:after{
      content: '';
      position: absolute;
      top: 2%;
      left: 3%;
      right: 3%;
      bottom: 3%;
    }
    &:before{
      top: 50%;
    }
  }
  &:hover {
    .thumb__overlay {
      opacity: 1;
      > *{
        @include transform(translateY(0));
      }
    }
    .thumb__overlay1 {
      opacity: 0;
    }
    .thumb__titel{
      opacity: 1;
    }
  }
}


