$map-height: 200px;
$map-xs-height: 250px;
$map-sm-height: 553px;

.rd-google-map {
  color: #333;
  &__model{
    height: $map-xs-height;

    img {
      max-width: none !important;
    }

    @media (min-width: $screen-xs-min) {
      height: $map-xs-height;
    }

    @media (min-width: $screen-sm-min) {
      height: $map-sm-height;
    }
  }

  &__locations{
    display: none;
  }
}

// Google Map
.google-map-markers{
  display: none;
}

.google-map-container {
  width: 100%;
}

.google-map {
  height: 200px;
  width: 100%;

  @include media-breakpoint-up(xs) {
    height: 250px;
  }

  @include media-breakpoint-up(xs) {
    height: 553px;
  }
}
